@use 'variables' as *;

@mixin flex-central-align {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@mixin cross-browser-support-transition-set {
    transition:                 $transition-03;
    -webkit-transition:         $transition-03;
    -moz-transition:            $transition-03;
    -o-transition:              $transition-03;
}